import React from "react";
import KeplerGl from "kepler.gl";
import { addDataToMap } from "kepler.gl/actions";
// import { useDispatch } from "react-redux";
import axios from "axios";
import useSWR from "swr";
import { useDispatch } from "react-redux";
import { KeplerGlSchema } from "kepler.gl/dist/schemas";

export default function Map() {
  const dispatch = useDispatch();
  
  const fetcher = url => axios.get(url).then(res => res.data)
  const { data } = useSWR(process.env.REACT_APP_SERVER_DATA, fetcher)

  React.useEffect(() => {
    
    if (data) {
      const mapToLoad = KeplerGlSchema.load(data.datasets, data.config);
      dispatch(addDataToMap(mapToLoad))
    }

  }, [dispatch, data])

  const token = process.env.REACT_APP_MAPBOX_API_ACCESS_TOKEN

  return (
    <KeplerGl
      id="kepler"
      mapboxApiAccessToken={token}
      width={window.innerWidth}
      height={window.innerHeight}
    />
  );
}